<template>
  <div>
    <vue-html2pdf
      :show-layout="false"
      :enable-download="true"
      :preview-modal="false"
      :pdf-quality="2"
      :manual-pagination="true"
      pdf-format="a6"
      pdf-orientation="portrait"
      pdf-content-width="800px"
      @progress="onProgress($event)"
      @hasDownloaded="hasDownloaded($event)"
      ref="html2Pdf"
    >
      <section slot="pdf-content">
        <div 
          class="page-width"
          v-for="(guide, index) in guides" :key="index"
        >
            <section id="header" class="header">
              <div class="row">
                <div class="col-md-6">
                  <img
                    src="@/assets/images/logo-black.png"
                    class="logo"
                    alt="..."
                  />
                </div>
                <div class="col-md-6">
                  <barcode
                    :value="guide.guideNumber"
                    :display-value="false"
                    format=""
                    width="1"
                    height="45"
                  ></barcode>
                </div>
              </div>
            </section>
            <div class="divider"></div>
            <section id="number-guide">
              <h4 class="text-center text-bold">{{ guide.guideNumber }}</h4>
            </section>
            <div class="divider"></div>
            <section id="remitente">
              <div class="row">
                <div class="col-md-6">
                  <h6 class="text-bold">Remitente:</h6>
                  <p class="m-0 p-0 small-text">{{ guide.remitente.name }}</p>
                  <p class="m-0 p-0 small-text">{{ guide.remitente.email }}</p>
                  <p class="m-0 p-0 small-text">
                    {{ guide.remitente.phoneNumber }}
                  </p>
                </div>
              </div>
            </section>
            <div class="divider"></div>
            <section id="destinatario">
              <div class="row">
                <div class="col-md-12">
                  <h6 class="text-bold">Destinatario:</h6>
                  <p class="m-0 p-0 small-text">
                    {{ guide.destinatario.name }}
                  </p>
                  <p class="m-0 p-0 small-text">
                    {{ guide.destinatario.phoneNumber }}
                  </p>
                  <p class="m-0 p-0 small-text">
                    {{ guide.destinatario.address }}
                  </p>
                  <p class="m-0 p-0 small-text">
                    {{ guide.destinatario.city }}
                  </p>
                  <p class="m-0 p-0 small-text">
                    {{ guide.destinatario.customerAnnotations }}
                  </p>
                </div>
              </div>
            </section>
            <div class="divider"></div>
            <section id="description">
              <div class="row">
                <div class="col-md-12">
                  <h6 class="m-0">Descripcin:</h6>
                  <p class="p-0 m-0 small-text">{{ textDescription(guide.description) }}</p>
                </div>
              </div>
            </section>
            <section id="total">
              <h4 class="text-bold text-center total">
                Valor a cobrar {{ calculateTotalGuide(guide.description) }}
              </h4>
            </section>
          <div class="html2pdf__page-break" v-if="guides.length > 1"></div>
        </div>
      </section>
    </vue-html2pdf>
    <div class="text-center">
      <button
        class="btn btn-success"
        @click="generateReport"
        :disabled="creating"
      >
        <template v-if="creating">
          <b-spinner small></b-spinner>
          Cargando...
        </template>
        <template v-else>Generar PDF</template>
      </button>
    </div>
  </div>
</template>
<script>
import VueHtml2pdf from "vue-html2pdf";
import VueBarCode from "vue-barcode";
import { formatPrice } from "@/utils/general";
import { MARK_PRINTED_GUIDE } from '@/api/graphql/mutations/guideLocal'
import { toast } from "@/utils/alerts";

export default {
  name: "Guide",
  props: {
    guides: {
      type: Array,
      default: [],
    },
  },
  components: {
    VueHtml2pdf,
    barcode: VueBarCode,
  },
  data() {
    return {
      creating: false,
    };
  },
  methods: {
    async generateReport() {
      this.creating = true;
      const success = await this.markPrintedGuides()
      if(!success) return toast('error')
      this.$refs.html2Pdf.generatePdf();
    },
    onProgress(event) {
      if (event === 100) {
        this.creating = false;
      }
    },
    calculateTotalGuide(products) {
      let total = 0;
      for (let item of products) {
        total += parseInt(item.price);
      }
      return formatPrice(total, "currency");
    },
    textDescription(products) {
      let text = "";
      for (let item of products) {
        text += `${item.name} X ${item.quantity} ${
          item.productObservations ? item.productObservations : ""
        }${products.length > 1 ? ";" : ""} `;
      }
      return text;
    },
    async markPrintedGuides(){
      try {
        const response = await this.$apollo.mutate({
          mutation: MARK_PRINTED_GUIDE,
          variables: {
            guides: this.getArrayGuides()
          }
        })
        const { markPrintedGuide } = response.data
        if (markPrintedGuide === null){
          toast('error', 'No se pudo actualizar el estado de las guas')
          return false
        }
        if(markPrintedGuide.success === false){
          toast('error', 'No se pudo actualizar el estado de las guas')
          return false
        }
        return true
      } catch (error) {
        toast('error', 'No se pudo actualizar el estado de las guas')
        return false
        
      }
    },
    getArrayGuides(){
      const guides = []
      for (let item of this.guides){
        guides.push(item.guideNumber)
      }
      return guides
    },
    hasDownloaded(event){
      this.$emit('success', true)
    }
  },
};
</script>
<style scoped>
.page-width {
  width: 100mm;
  height: 148mm;
  max-height: 148mm;
  margin-left: 10px;
  position: relative;
}
.logo {
  max-width: 45mm;
}
.small-text {
  font-size: small;
}
.divider {
  border: 1px solid black;
}
.text-bold {
  font-weight: bold;
}
.total {
  position: absolute;
  bottom: 5mm;
  left: 12mm;
}
</style>
