<template>
  <layout>
    <page-header :title="title"></page-header>
    <div>
      <b-col md="12">
        <b-card class="mb-2">
          <b-card-text>
            <form-wizard>
              <wizard-step 
                slot-scope="props"
                slot="step"
                :tab="props.tab"
                :transition="props.transition"
                :index="props.index">
              </wizard-step>
              <tab-content title="Crear Gua" :before-change="() => validate()">
                <b-row>
                  <b-col md="6">
                    <b-form-group
                      label="Nombre *"
                      invalid-feedback="Este campo es requerido"
                    >
                      <b-form-input
                        v-model="formGuide.name"
                        type="text"
                        :state="state($v.formGuide.name)"
                      >
                      </b-form-input>
                    </b-form-group>
                  </b-col>
                  <b-col md="6">
                    <b-form-group
                      label="Nmero de telfono *"
                      invalid-feedback="Este campo es requerido"
                    >
                      <b-form-input
                        v-model="formGuide.phoneNumber"
                        type="number"
                        :state="state($v.formGuide.phoneNumber)"
                      ></b-form-input>
                    </b-form-group>
                  </b-col>
                  <b-col md="6">
                    <b-form-group
                      label="Correo electrnico"
                      invalid-feedback="Email invalido"
                    >
                      <b-form-input
                        v-model="formGuide.email"
                        type="email"
                        :state="state($v.formGuide.email)"
                      ></b-form-input>
                    </b-form-group>
                  </b-col>
                  <b-col md="6">
                    <b-form-group
                      label="Ciudad *"
                      invalid-feedback="Este campo es requerido"
                    >
                      <b-form-select
                        v-model="formGuide.city"
                        :options="city"
                        class="form-select"
                        :state="state($v.formGuide.city)"
                      ></b-form-select>
                    </b-form-group>
                  </b-col>
                  <b-col md="6">
                    <b-form-group
                      label="Direccin *"
                      invalid-feedback="Este campo es requerido"
                    >
                      <b-form-input
                        v-model="formGuide.address"
                        type="text"
                        :state="state($v.formGuide.address)"
                      ></b-form-input>
                    </b-form-group>
                  </b-col>
                  <b-col md="6">
                    <b-form-group
                      label="Forma de pago *"
                      invalid-feedback="Este campo es requerido"
                    >
                      <b-form-select
                        v-model="formGuide.paymentType"
                        :state="state($v.formGuide.paymentType)"
                        :options="paymentTypes"
                        class="form-select"
                      ></b-form-select>
                    </b-form-group>
                  </b-col>
                  <b-col md="12">
                    <b-form-group
                      label="Anotaciones sobre el cliente"
                      invalid-feedback="Este campo es requerido"
                    >
                      <b-form-textarea
                        v-model="formGuide.customerAnnotations"
                      ></b-form-textarea>
                    </b-form-group>
                  </b-col>
                  <!-- <b-col md="6">
                    <b-form-group
                      label="Observaciones de entrega"
                      invalid-feedback="Este campo es requerido"
                    >
                      <b-form-textarea
                        v-model="formGuide.deliveryObservations"
                      ></b-form-textarea>
                    </b-form-group>
                  </b-col> -->
                </b-row>
              </tab-content>
              <tab-content
                title="Agregar Productos"
                :before-change="() => createGuideLocal()"
              >
                <multiselect
                  :options="allProducts"
                  :custom-label="customLabelInputProduct"
                  :close-on-select="false"
                  placeholder="Selecciona un producto"
                  @select="onSelect"
                  ref="multiselect"
                  id="multiselect"
                  :max-height="150"
                ></multiselect>
                <div style="margin-top: 150px" v-show="productSelected.length">
                  <b-table
                    sticky-header
                    striped
                    hover
                    :items="productSelected"
                    :fields="fields"
                  >
                    <template v-slot:cell(price)="{ item }">
                      {{ formatPriceNumber(item.price, "currency") }}
                    </template>
                    <template v-slot:cell(actions)="{ item }">
                      <b-btn @click="updateRowTable(item)" variant="info">
                        <i class="mdi mdi-pencil"></i>
                      </b-btn>
                      <b-btn
                        @click="deleteRowTable(item)"
                        variant="danger"
                        style="margin-left: 2px"
                      >
                        <i class="mdi mdi-delete"></i>
                      </b-btn>
                    </template>
                  </b-table>
                </div>
              </tab-content>
              <tab-content title="Gua">
                <guide
                  :guides="guidePrint"
                ></guide>
              </tab-content>
              <template slot="footer" slot-scope="props">
                <div class="wizard-footer-left">
                  <wizard-button
                    v-if="props.activeTabIndex > 0 && !props.isLastStep"
                    :style="props.fillButtonStyle"
                    @click.native="props.prevTab()"
                  >
                    Anterior
                  </wizard-button>
                </div>
                <div class="wizard-footer-right">
                  <wizard-button
                    v-if="!props.isLastStep"
                    @click.native="props.nextTab()"
                    class="wizard-footer-right"
                    :style="props.fillButtonStyle"
                    :disabled="
                      creatingGuide ||
                      (!formGuide.products.length && props.activeTabIndex === 1)
                    "
                  >
                    <template v-if="creatingGuide">
                      <b-spinner small type="grow"></b-spinner>
                      Cargando...
                    </template>
                    <template v-else> Siguiente </template>
                  </wizard-button>
                  <wizard-button
                    v-else
                    @click.native="onComplete()"
                    class="wizard-footer-right finish-button"
                    :style="props.fillButtonStyle"
                    >{{
                      props.isLastStep ? "Finalizar" : "Siguiente"
                    }}</wizard-button
                  >
                </div>
              </template>
            </form-wizard>
          </b-card-text>
        </b-card>
      </b-col>
    </div>
    <b-modal
      id="productInfo"
      title="Informacin"
      @ok="handleOk"
      ok-only
      return-focus="#multiselect"
      :modal-cancel="false"
      :no-close-on-backdrop="true"
    >
      <div class="container">
        <b-row>
          <b-col md="6">
            <b-form-group
              label="Cantidad"
              invalid-feedback="Ingresa un valor valido"
            >
              <b-form-input
                v-model="formProducts.quantity"
                type="number"
                :state="state($v.formProducts.quantity)"
                autofocus
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group
              label="Precio"
              invalid-feedback="Este campo es requerido"
            >
              <b-form-input
                v-model="formProducts.price"
                required
                :state="state($v.formProducts.price)"
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col md="12">
            <b-form-group
              label="Observaciones del producto"
              invalid-feedback="Este campo es requerido"
            >
              <b-form-textarea
                v-model="formProducts.productObservations"
              ></b-form-textarea>
            </b-form-group>
          </b-col>
        </b-row>
      </div>
    </b-modal>
  </layout>
</template>
<script>
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import { required, email, minValue } from "vuelidate/lib/validators";
import { FormWizard, TabContent, WizardButton, WizardStep } from "vue-form-wizard";
import { toast } from "@/utils/alerts";
import { decode_bs64 } from "@/utils/general";
import { ALL_PRODUCTS_CURRENT_STORE_USER } from "@/api/graphql/query/products";
import Multiselect from "vue-multiselect";
import "vue-form-wizard/dist/vue-form-wizard.min.css";
import { CREATE_GUIDE_LOCAL } from "@/api/graphql/mutations/guideLocal";
import { storeComputed } from "@/state/helpers";
import Guide from "@/components/widgets/guide";
import { formatPrice, formatCurrency } from "@/utils/general";

export default {
  components: {
    Layout,
    PageHeader,
    FormWizard,
    TabContent,
    WizardButton,
    WizardStep,
    Multiselect,
    Guide,
  },
  data() {
    return {
      title: "Gua local",
      formGuide: {
        phoneNumber: null,
        name: null,
        city: null,
        address: null,
        email: null,
        customerAnnotations: null,
        paymentType: null,
        deliveryObservations: null,
        products: [],
        store: null,
      },
      formProducts: {
        product: null,
        quantity: null,
        price: null,
        productObservations: null,
      },
      allProducts: [],
      lastCursor: null,
      product: null,
      productSelected: [],
      fields: [
        {
          key: "name",
          label: "Producto",
        },
        {
          key: "quantity",
          label: "Cantidad",
        },
        {
          key: "price",
          label: "Precio",
        },
        {
          key: "productObservations",
          label: "Observaciones del producto",
        },
        {
          key: "actions",
          label: "Acciones",
        },
      ],
      city: [
        { value: null, text: "Selecciona una opcin" },
        { value: "Bogota", text: "Bogot" },
      ],
      paymentTypes: [
        { value: null, text: "Selecciona una opcin" },
        { value: "ContraEntrega", text: "Contra Entrega" },
      ],
      hasNextPage: false,
      creatingGuide: false,
      guidePrint: []
    };
  },
  validations: {
    formGuide: {
      phoneNumber: { required },
      name: { required },
      city: { required },
      email: { email },
      address: { required },
      paymentType: { required },
    },
    formProducts: {
      quantity: { required, minValue: minValue(1) },
      price: { required },
      product: { required },
    }
  },
  computed: {
    ...storeComputed,
    productList(){
      return this.formGuide.products
    },
    productPrice(){
      return this.formProducts.price
    }
  },
  watch: {
    productList: function(newValue){
      this.totalGuide = this.getTotalGuide(newValue)
    },
    productPrice: function(newValue){
      if (newValue){
        this.formProducts.price = formatCurrency(newValue)
      }
    }
  },
  methods: {
    cleanPrice(){
      const price = this.formProducts.price.replaceAll(',', '')
      this.formProducts.price = price
    },
    handleOk(bvModalEvent) {
      bvModalEvent.preventDefault();
      this.$v.formProducts.$touch();
      const isValid = !this.$v.formProducts.$invalid;
      if (isValid === true) {
        this.handleSubmit();
      }
      return isValid;
    },
    handleSubmit() {
      const indexProduct = this.productSelected.findIndex(
        (p) => p.product === this.formProducts.product
      );
      this.cleanPrice()
      const temp = {
        name: this.product.name,
        ...this.formProducts,
      };
      if (indexProduct === -1) {
        this.productSelected.push(temp);
        this.formGuide.products.push({ ...this.formProducts });
      } else {
        this.productSelected.splice(indexProduct, 1, temp);
        this.formGuide.products.splice(indexProduct, 1, this.formProducts);
      }
      this.$nextTick(() => {
        this.$bvModal.hide("productInfo");
      });
      this.formProducts = {
        product: null,
        quantity: null,
        price: null,
        productObservations: null,
      };
      this.$v.formProducts.$reset();
    },
    state(item) {
      const { $dirty, $error } = item;
      return $dirty ? !$error : null;
    },
    async validate() {
      this.$v.formGuide.$touch();
      const isValid = !this.$v.formGuide.$invalid;
      this.formGuide.store = this.idCurrentStore;
      return isValid;
    },
    async getProducts() {
      try {
        const response = await this.$apollo.query({
          query: ALL_PRODUCTS_CURRENT_STORE_USER,
          variables: {
            cursor: this.lastCursor,
          },
        });
        const { listProducts } = response.data;
        if (listProducts !== null) {
          const data = listProducts.edges;
          this.hasNextPage = listProducts.pageInfo.hasNextPage;
          this.addArrayProducts(data);
          if (this.hasNextPage) {
            this.getProducts();
          } else {
            this.loadingProducts = false;
          }
        } else {
          toast("error", "No se pudo recuperar la informacin");
          this.loadingProducts = false;
        }
      } catch (error) {
        this.loadingProducts = false;
        toast("error", "No se pudo recuperar la informacin");
      }
    },
    addArrayProducts(data) {
      for (let product of data) {
        const { node } = product;
        const temp = {
          id: node.id,
          name: node.name,
          price: node.price,
          stock: node.stock,
          sku: node.sku,
        };
        this.allProducts.push(temp);
        this.lastCursor = product.cursor;
      }
    },
    customLabelInputProduct(product) {
      return product.name;
    },
    onSelect(option) {
      const validate = this.validateProductNotInArray(option);
      if (!validate) {
        return;
      }
      this.product = option;
      this.formProducts.product = parseInt(
        decode_bs64(option.id).split(":")[1]
      );
      this.$bvModal.show("productInfo");
    },
    validateProductNotInArray(product) {
      const productId = parseInt(decode_bs64(product.id).split(":")[1]);
      const index = this.productSelected.findIndex(
        (p) => p.product === productId
      );
      if (index !== -1) {
        toast("error", "El producto ya se encuentra agregado.");
        return false;
      }
      return true;
    },
    updateRowTable(product) {
      this.formProducts = {
        product: product.product,
        price: product.price,
        quantity: product.quantity,
        productObservations: product.productObservations,
      };
      this.$bvModal.show("productInfo");
    },
    deleteRowTable(product) {
      const index = this.productSelected.findIndex(
        (p) => p.product === product.product
      );
      this.productSelected.splice(index, 1);
      this.formGuide.products.splice(index, 1);
    },
    async createGuideLocal() {
      this.creatingGuide = true;
      try {
        const response = await this.$apollo.mutate({
          mutation: CREATE_GUIDE_LOCAL,
          variables: {
            guideLocal: {
              ...this.formGuide,
            },
          },
        });
        const { createGuideLocal } = response.data;
        if (createGuideLocal === null) {
          toast("error", "No se pudo crear la gua");
          this.creatingGuide = false;
          return false;
        }
        if (createGuideLocal.success == false) {
          toast("error", "No se pudo crear la gua");
          this.creatingGuide = false;
          return false;
        }
        this.formatGuideToPrint(createGuideLocal.guideLocal)
        this.creatingGuide = false;
        return true;
      } catch (error) {
        toast("error", "No se pudo guardar la gua");
        this.creatingGuide = false;
        return false;
      }
    },
    formatPriceNumber(value, type){
      return formatPrice(value, type)
    },
    getTotalGuide(data){
      let total = 0
      for(let item of data){
        total += parseInt(item.price)
      }
      return total
    },
    onComplete(){
      this.$router.push({
        name: 'guide-local-list'
      })
    },
    formatGuideToPrint(guide){
      const formatArrayProducts = ( products ) =>{
        const productsArray = []
        for (let item of products){
          const temp = {
            name: item.node.product.name,
            price: item.node.price,
            quantity: item.node.quantity,
            productObservations: item.node.productObservations
          }
          productsArray.push(temp)
        }
        return productsArray
      }
      const temp = {
        guideNumber: guide.guideNumber,
        destinatario: {
          name: guide.name,
          phoneNumber: guide.phoneNumber,
          address: guide.address,
          city: guide.city,
          customerAnnotations: guide.customerAnnotations
        },
        remitente: {
          name: guide.store.name,
          phoneNumber: guide.store.phoneNumber,
          email: guide.store.email,
        },
        description: formatArrayProducts(guide.guidelocalproductthroughSet.edges)
      }
      this.guidePrint.push(temp)
    }
  },
  beforeMount() {
    this.getProducts();
  },
};
</script>